import React from 'react'
import Img from 'assets/icons/sayur-box-logo.svg'
import { isFloat } from 'services'

const UMSummaryCard = ({ data, setUser }) => {
    const image = Img
    const mobile = true
    const userName = 'sugiana@gmail.com'
    const userArea = 'Admin - JK01 - Sentul'
    const modules = data.key || 0
    const category = 12
    return (
        <div onKeyDown={(e) => e.keyCode === 13 ? alert() : false} tabindex="0" className={`w-100 border d-md-flex sku-item um-list-card justify-content-between ${mobile ? 'pl-0 pr-0' : 'pl-2 pr-3'} py-2 mb-3 rounded`}>
            <div className={`d-flex align-items-center w-100 cursor-pointer ${mobile ? 'pl-2 pr-3' : 'pl-0 pr-0'}`} onClick={(alert)}>
                <img
                    src={image} alt='sku image'
                    className='w-5 mr-3 border softGreen rounded' />
                <div className='text-left'>
                    <div className='mr-3 text LatoBold fontSizeTitle'>{userName}</div>
                    <div className='text'>{userArea}</div>
                </div>
            </div>
            <hr className={`${mobile ? null : 'd-none'}`} />
            <div className={`d-flex w-100 ${mobile ? 'pr-3' : 'pr-0'} pl-5 pl-md-0 align-items-center`}>
                <div className={`d-flex justify-content-md-end align-items-center w-100 ${mobile ? 'justify-content-end' : null}`}>
                    <div className='mr-1 font-weight-bold emptyTextField'>{`${modules} Modules`}</div>
                    <div className={`mr-3 ml-3 emptyTextField iconU-sideBarDot d-flex align-items-center`} />
                    <div className='mr-3 font-weight-bold emptyTextField'>{`${category} SKU Categories`}</div>
                </div>
                <div onClick={() => setUser(data)} style={{ fontSize: 20 }} className={`cursor-pointer iconU-tableEdit font-weight-bold green d-flex align-items-center justify-content-center`}></div>
            </div>
        </div>
    )
}

export default UMSummaryCard