import React from 'react'
import { CCol, CModal, CModalBody, CModalHeader, CRow, CSwitch, CButton } from "@coreui/react";
import { heightStyle } from './services'
import Img from 'assets/icons/sayur-box-logo.svg'
import Dropdown from 'Component/Dropdown'
import { useState } from 'react';
import { useSelector } from 'react-redux';

const UMEdit = ({ setUser, user }) => {
    const inventoryModules = useSelector(state => state.inventoryModules)
    const categoryList = useSelector(state => state.categoryList)
    const userRoles = useSelector(state => state.userRoles)
    const categoryAccess = ['Carbs', 'Bawang X', 'Herbal', 'Dairy and Eggs', 'Dll', 'Carbs', 'Bawang X', 'Herbal', 'Dairy and Eggs', 'Dll', 'Carbs', 'Bawang X', 'Herbal', 'Dairy and Eggs', 'Dll']
    const [category, setCategory] = useState('')
    const submit = true

    const width = document.documentElement.clientWidth
    let height = document.documentElement.clientHeight - 130
    const categoryHeight = width <= 500 ? '60%' : '75%'
    return (
        <CRow onKeyDown={e => e.keyCode === 27 ? setUser() : false} className={`${user ? null : 'd-none'} single-view`}>
            <CCol>
                <CModal
                    closeOnBackdrop={false}
                    show={user}
                    onClose={() => setUser()}
                    className='w-100'
                >
                    <CModalHeader className="px-3 pt-3  border-none m-0 d-flex align-items-center">
                        <div className='LatoBold softGrey'>User Management - Edit User</div>  <i className="iconU-modalClose LatoBold" onClick={() => setUser()} />
                    </CModalHeader>
                    <CModalBody className="pl-3 pr-1 py-0 w-100 overflow-y border-top pt-3 pb-3" style={heightStyle(height + 15)}>
                        <div className=' d-flex align-items-center bg-softGreen rounded p-3 mb-3'>
                            <div className={`d-flex align-items-center w-100 cursor-pointer ${width <= 500 ? 'pl-2 pr-3' : 'pl-0 pr-0'}`} onClick={(alert)}>
                                <img
                                    src={Img} alt='sku image'
                                    className='w-5 mr-3 border softGreen rounded' />
                                <div className='text-left'>
                                    <div className='mr-3 text LatoBold fontSizeTitle'>{'sugiana@gmail.com'}</div>
                                    <div className='text'>{'Admin - JK01 - Sentul'}</div>
                                </div>
                            </div>
                        </div>

                        <div style={{ backgroundColor: '#F9FAFC' }} className='mb-3 p-3 rounded  d-flex justify-content-between align-items-center'>
                            <div style={{ height: 'fit-content' }} className='LatoBold'>User Level Access</div>
                            <Dropdown show={true} width='w-50' placeholder='Select Role' style={{}} noMarginBottom={true} options={userRoles} title='User Roles' />
                        </div>

                        <div style={{ backgroundColor: '#F9FAFC' }} className='mb-3 p-3 rounded  h-25'>
                            <div style={{ height: 'fit-content' }} className='LatoBold pb-3'>User Module Access <span>(7 Modules Selected)</span></div>
                            <div className='overflow-y' style={heightStyle('75%')}>
                                <div className='grid-container'>
                                    {
                                        inventoryModules?.map(data => (
                                            <div className='d-flex align-items-center mb-3'>
                                                <table>
                                                    <td style={{ verticalAlign: 'middle' }}>
                                                        <CSwitch
                                                            className="float-right mr-3"
                                                            shape="pill"
                                                            color="success" size={width <= 500 ? "sm" : "md"}
                                                            // checked={true}
                                                            onChange={() => false} /></td>

                                                    <td style={{ verticalAlign: 'middle' }}><div className='text' style={{ fontWeight: 400 }}>{data.label}</div></td>
                                                </table>
                                            </div>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>

                        <div style={{ backgroundColor: '#F9FAFC', height: '38%' }} className='mb-3 p-3 rounded '>
                            <div style={{ height: 'fit-content' }} className='LatoBold mb-2'>SKU Category Access <span>(7 Categories Selected)</span></div>

                            <div className={` border rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mb-3`}>
                                <input autoComplete='off' placeholder='Enter Category' className='w-100 plain-input' onChange={(e) => setCategory(e.target.value)} />
                            </div>

                            <div className='overflow-y' style={heightStyle(categoryHeight)}>
                                <div className='grid-container'>
                                    {
                                        categoryList?.filter(data => {
                                            if (data?.label?.toLowerCase().includes(category)) return data
                                        })?.map(data => (
                                            <div className='d-flex align-items-center mb-3'>
                                                <table>
                                                    <td style={{ verticalAlign: 'middle' }}>
                                                        <CSwitch
                                                            className="float-right mr-3"
                                                            shape="pill"
                                                            color="success" size={width <= 500 ? "sm" : "md"}
                                                            // checked={true}
                                                            onChange={() => false} /></td>

                                                    <td style={{ verticalAlign: 'middle' }}><div className='text' style={{ fontWeight: 400 }}>{data.label}</div></td>
                                                </table>
                                            </div>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>

                        <CButton onClick={() => null} className={`${submit ? 'bg-green text-white' : 'bg-softGreen text'} no-shadow sku-item px-5 w-100`}> Submit</CButton>

                    </CModalBody>
                </CModal>
            </CCol>
        </CRow >
    )
}

export default UMEdit